import { useEffect, useState } from "react";

export default function Header() {
  const [settings, setSettings] = useState({ siteTitle: "Staff Scheduler", logo: null });

  useEffect(() => {
    fetch("/api/settings").then((r) => r.json()).then((d) => { if (d.ok) setSettings(d.settings || {}); });
  }, []);

  return (
    <header style={{ background: "#0b5cff", color: "white", padding: "12px 24px", marginBottom: 18 }}>
      <div style={{ maxWidth: 1000, margin: "0 auto", display: "flex", alignItems: "center", gap: 12 }}>
        {settings.logo ? <img src={settings.logo} alt="logo" style={{ height: 40, borderRadius: 6 }} /> : <div style={{ height: 40, width: 40, background: "white", color: "#0b5cff", display: "flex", alignItems: "center", justifyContent: "center", borderRadius: 6, fontWeight: 700 }}>SO</div>}
        <div style={{ fontWeight: 700, fontSize: 18 }}>{settings.siteTitle || "Staff Scheduler"}</div>
        <div style={{ marginLeft: "auto" }}>
          <a href="/" style={{ color: "white", marginRight: 12 }}>Home</a>
          <a href="/calendar" style={{ color: "white", marginRight: 12 }}>Calendar</a>
          <a href="/checkin" style={{ color: "white", marginRight: 12 }}>Check In</a>
          <a href="/admin" style={{ color: "white" }}>Admin</a>
        </div>
      </div>
    </header>
  );
}


